
.pretty {
  margin-right: 20px;
  cursor: pointer;

  .state {

    &::before {
      top: -0.5em !important;
      height: 1em!important;
    }

    label {
      &::before {
        top: -0.5em !important;
      }

      &::after {
        top: -0.5em !important;
        cursor: pointer;
      }
    }
  }

}