$bg-color: #F6F7FB;
$text-color: #495057;
$description-color: #838999;
$btn-color: #726DC1;
$input-color: #E7EBF6;
$focus-input-color: #dde5f8;
$input-text-color: #28282c;
$transition-standard: 0.1s linear;
$standard-height: calc(100vh - 131px);

@mixin fontStyle($f-size, $lh, $fw, $mb, $color) {
  color: $color;
  font-size: $f-size;
  font-weight: $fw;
  margin-bottom: $mb;
  line-height: $lh;
  font-style: normal;
}

%flex-element-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
}

nav {
  .navBar__currentPageContainer {
    @extend %flex-element-center;
    padding: 10px 0;
    border-bottom: 1px solid #E7EBF6;
    @include fontStyle(20px, 29px, 500, 0px, $text-color);
    margin-bottom: 20px;
  }


  .navBar__container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    background: $bg-color;
    box-sizing: border-box;
    padding: 10px 20px;
    border-bottom: 1px solid #E7EBF6;

    .active_link {
      background-color: #dde5f8;

    }


    li, button {
      .navBar__container_ToolTip {
        z-index: 10000;
        position: absolute;
        background: #2F2F30;
        padding: 5px 10px;
        @include fontStyle(14px, 16px, 500, 0px, white);
        text-align: center;
        border-radius: 5px;
        top: 50px;
        @extend %flex-element-center;
        letter-spacing: 1px;
        transition: $transition-standard;
        transform: scale(0);
        opacity: 0;

        span {
          position: absolute;
          width: 0;
          height: 0;
          border-left: 8px solid transparent;
          border-right: 8px solid transparent;
          border-bottom: 20px solid #2F2F30;
          top: -50%;
          transform: translateY(50%);

        }
      }

      width: 40px;
      height: 40px;
      @extend %flex-element-center;
      background: transparent;
      transition: $transition-standard;
      border-radius: 5px;
      border: none;
      cursor: pointer;
      position: relative;

      a {
        @extend %flex-element-center;
        width: 100%;
        height: 100%;
      }

      &:hover {
        background-color: #dde5f8;
      }

      &:hover .navBar__container_ToolTip {
        transform: scale(1);
        opacity: 1;
      }
    }


  }
}

.main__container {
  max-width: 500px;
  margin-right: auto;
  margin-left: auto;
  background: $bg-color;
  max-height: 100vh;
}

.login__container {
  @extend %flex-element-center;
  flex-direction: column;
  padding: 0 20px;
  height: 100vh;


  div {
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
  }

  h1 {
    @include fontStyle(20px, 29px, 500, 0px, $text-color);
    margin-left: 10px;
  }

  h2 {
    @include fontStyle(18px, 26px, 500, 10px, $text-color);
  }

  span {
    @include fontStyle(14px, 20px, 400, 0px, $description-color);
  }

  button {
    width: 100%;
    @include fontStyle(14px, 20px, 500, 0px, white);
    background: $btn-color;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    padding: 13px 0;
    margin: 35px 0;
    cursor: pointer;
    transition: 0.1s linear;

    &:hover {
      background-color: #7b75ef;
    }
  }
}

.loader__containerInButton {
  background: transparent;
  @extend %flex-element-center;
  margin-left: auto;
  margin-right: auto;

  .lds_dual_ring {
    display: inline-block;
    width: 20px;
    height: 20px;
    @extend %flex-element-center;

    &:after {
      content: " ";
      display: block;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      border: 3px solid #fff;
      border-color: #fff transparent #fff transparent;
      animation: lds-dual-ring 1.2s linear infinite;
    }
  }

  @keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

}

.loader__container {
  @extend %flex-element-center;
  height: $standard-height;
  background-color: $bg-color;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;

  .lds_ripple {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;

    div {
      &:nth-child(2) {
        animation-delay: -0.5s;
      }

      position: absolute;
      border: 4px solid $btn-color;
      opacity: 1;
      border-radius: 50%;
      animation: lds_ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
    }
  }

  @keyframes lds_ripple {
    0% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    4.9% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 0;
    }
    5% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0;
      left: 0;
      width: 72px;
      height: 72px;
      opacity: 0;
    }
  }
}

.chat__container_noSearches {

  height: calc(100vh - 211px);

  background-color: #f8f8f8;
  @extend %flex-element-center;

  div {
    @include fontStyle(16px, 23px, 500, 0px, $text-color);
  }
}


.chat__container {
  display: flex;
  flex-direction: column-reverse;
  padding: 0 5px 20px 5px;
  height: calc(100vh - 211px);

  background: #FFFAFA;
  overflow: auto;

  .chat__messageContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    transition: $transition-standard;


    span {
      padding-right: 5px;
      @include fontStyle(14px, 20px, 500, 0px, $text-color);
    }
  }

  .chat__containerAvatar {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #afb4c5;
    color: black;
    font-weight: 600;
  }

  .chat__containerItem {
    display: flex;
    padding: 10px 20px;
    justify-content: flex-end;
    position: relative;

  }

  .chat__containerSelectedImg {
    position: absolute;
    background: transparent;
    width: 20px;
    height: 20px;
    border: 1px solid #babaff;
    border-radius: 50%;
    bottom: 10px;
    left: 10px;
    transition: 0.2s linear;
    transform: translateX(-50px);

    img {
      width: 100%;
      height: 100%;
      transition: 0.2s linear;
      opacity: 0;
    }
  }





  .chat__containerMessageText {
    padding: 10px;
    border-radius: 5px;
    background-color: #b4ceff;
    display: flex;
    flex-direction: column;
    width: 55vw;
    max-width: 210px;
    word-break: break-word;

    @include fontStyle(14px, 20px, 500, 0px, $text-color);
    align-items: flex-end;

    span {
      @include fontStyle(13px, 19px, 400, 0px, $description-color);

      img {
        margin-right: 5px;
      }
    }
  }

  .chat__containerWithImg {
    display: flex;
    align-items: flex-end;
    margin-left: 10px;
  }


}

.messageField__container {
  padding: 18px 20px;
  display: flex;
  border-bottom: 1px solid #E7EBF6;
  border-top: 1px solid #E7EBF6;

  button {
    min-width: 41px;
    height: 41px;
    background-color: $btn-color;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    cursor: pointer;
    transition: 0.1s linear;
    margin-left: 20px;

    &:hover {
      background-color: #7b75ef;
    }
  }

  input {
    width: 100%;
    background-color: $input-color;
    padding: 11px;
    @include fontStyle(14px, 20px, 400, 0px, $input-text-color);
    border-radius: 5px;
    border: none;
    transition: 0.1s linear;

    &:focus {
      outline: none;
      background-color: $focus-input-color;
    }

    &:hover {
      background-color: $focus-input-color;
    }
  }

}

.users__inputContainer {
  @extend %flex-element-center;
  position: relative;

  img {
    position: absolute;
    left: 40px;
  }

  input {
    background: transparent;
    @include fontStyle(15px, 22px, 500, 0px, $text-color);
    border: none;
    width: calc(100% - 40px);
    padding: 5px 20px 5px 50px;
    transition: 0.1s linear;
    background: $input-color;
    border-radius: 5px;

    &:hover:not([disabled]) {
      background: $focus-input-color;
    }

    &::placeholder {
      @include fontStyle(16px, 23px, 400, 0px, $description-color);
    }

    &:focus {
      outline: none;
      background: $focus-input-color;
    }
  }
}

.users__container {
  height: calc(100vh - 163px);
  display: flex;
  flex-direction: column;
  padding: 20px;


  .inMyContacts__description {
    @extend %flex-element-center;
    height: 100%;
    min-width: 135px;
    @include fontStyle(12px, 18px, 500, 0px, $btn-color);
  }

  li {

    transition: 0.1s linear;
    cursor: pointer;
    border-radius: 5px;


    a {
      padding: 15px 20px;
      @extend %flex-element-center;
      justify-content: space-between;
      text-decoration: none;
      position: relative;
    }

    &:hover {
      background: $input-color;
    }

    div {
      display: flex;
      align-items: center;
    }

    img {

      width: 40px;
      height: 40px;
      border-radius: 50%;
      margin-right: 15px;

    }

    .users__container_imgContainer {
      position: relative;

      .users__container_status {
        position: absolute;
        width: 15px;
        height: 15px;
        padding: 2px;
        background: #F6F7FB;
        border: none;
        bottom: 0;
        right: 0;
      }
    }

  }


  .message {
    text-decoration: none;
    @extend %flex-element-center;
    border: none;
    border-radius: 5px;
    background-color: $btn-color;
    transition: $transition-standard;
    @include fontStyle(14px, 20px, 400, 0, white);
    padding: 10px 25px;
    cursor: pointer;


    &:hover {
      background-color: #7b75ef;
    }
  }

  .users__containerWithInfoAboutUser {
    display: flex;
    flex-direction: column;
    @include fontStyle(15px, 22px, 500, 0px, $text-color);
    align-items: flex-start;

    span {
      @include fontStyle(14px, 20px, 400, 0px, $description-color);
    }
  }

  .users__container_date {
    position: absolute;
    top: 15px;
    right: 20px;
    @include fontStyle(14px, 20px, 400, 0px, $description-color);
  }

}

.editProfile__mainContainer {
  position: absolute;
  top: 61px;
  height: calc(100vh - 61px);
  width: 100%;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  padding: 80px 20px 0;
  background: $bg-color;
  max-width: 500px;


  h2 {
    width: 100%;
    @include fontStyle(20px, 29px, 500, 16px, $text-color);
  }

  .editProfile__container {
    @extend %flex-element-center;
    flex-direction: column;
    width: 100%;
    justify-content: flex-start;
  }

  button {
    @extend %flex-element-center;
    border: none;
    border-radius: 5px;
    background-color: $btn-color;
    transition: $transition-standard;
    @include fontStyle(14px, 20px, 500, 0, white);
    padding: 10px 25px;
    cursor: pointer;

    &:not([disabled]):hover {
      background-color: #7b75ef;
    }

    &:disabled {
      opacity: 0.65;
      cursor: auto;
    }
  }

  ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    background: white;
    padding: 20px 20px 0 20px;
    position: relative;
    border-radius: 5px;
    margin-bottom: 20px;

    .myProfile__namePropContainer {
      display: flex;
    }

    .editProfile__private {
      position: absolute;
      @include fontStyle(13px, 19px, 400, 0px, $description-color);
      top: 20px;
      right: 22px;
    }

    li {
      margin-bottom: 20px;

      h3 {
        display: flex;
        align-items: center;

        img {
          width: 19px;
          height: 19px;
          margin-left: 5px;
        }
      }

      span {
        display: flex;
      }

      div {
        display: flex;
        align-items: center;


        input {
          @include fontStyle(14px, 20px, 500, 0px, $text-color);
          padding: 10px 16px;
          border-top-right-radius: 5px;
          border-bottom-right-radius: 5px;
          border: 1px solid #E7EBF6;
          width: 75%;
          background-color: $bg-color;
          border-left: none;
          transition: $transition-standard;
          margin-right: 25px;

          &:focus {
            outline: none;
          }

          &:hover {
            background-color: $focus-input-color;
          }
        }

        span {
          @extend %flex-element-center;
          padding: 10px;
          border-top-left-radius: 5px;
          border-bottom-left-radius: 5px;
          border: 1px solid #E7EBF6;
          background-color: $bg-color;

        }
      }

      .placeholder__description {
        @include fontStyle(15px, 22px, 400, 0px, $description-color);
        padding: 4px 10px 6px 10px;
        border: none;
        background: transparent;
        font-stretch: normal;
        letter-spacing: 0.25px;
      }

      h3 {
        @include fontStyle(13px, 19px, 400, 0px, $description-color);
        margin-bottom: 8px;
      }
    }


  }

}

.userProfile__topContainer {
  @extend %flex-element-center;
  justify-content: space-between;
  position: absolute;
  width: calc(100% - 40px);
  top: 20px;

  button, a {
    @extend %flex-element-center;
    transition: $transition-standard;
    background: transparent !important;
    border: none;
    border-radius: 5px;
    width: 40px !important;
    height: 40px !important;
    margin: 0 !important;
    padding: 0 !important;

    &:hover {
      background: #dde5f8 !important;
    }

    img {
      transform: rotate(180deg);
      width: 30px;
      height: 30px;
      cursor: pointer;
    }
  }
}

.userProfile__container {
  position: absolute;
  top: 61px;
  height: calc(100vh - 61px);
  width: 100%;
  @extend %flex-element-center;
  flex-direction: column;
  padding: 0 40px;
  background: $bg-color;
  max-width: 500px;

  .userProfile__mainContainer {
    background-color: white;
    @extend %flex-element-center;
    flex-direction: column;
    width: 100%;
    padding: 32px 20px 20px 20px;

    a {
      text-decoration: none;
      @extend %flex-element-center;
      border: none;
      border-radius: 5px;
      background-color: $btn-color;
      transition: $transition-standard;
      @include fontStyle(14px, 20px, 400, 0, white);
      padding: 10px 25px;
      cursor: pointer;
      margin: 25px 0;

      &:hover {
        background-color: #7b75ef;
      }
    }

    ul {
      width: 100%;
      display: flex;
      flex-direction: column;
      background: white;
      margin-top: 30px;
      position: relative;
      border-radius: 5px;


      li {
        margin-bottom: 20px;
        display: flex;
        align-items: center;

        &:not(:last-child) {
          border-bottom: 1px solid #E7EBF6;;
        }

        img {
          margin-right: 15px;
        }

        .placeholder__description {
          @include fontStyle(15px, 22px, 400, 0px, $text-color);
          padding: 4px 10px 6px 0;
          border: none;
          background: transparent;
          font-stretch: normal;
          letter-spacing: 0.25px;
        }
      }

      h2 {
        @include fontStyle(14px, 20px, 400, 0px, $description-color);
        padding-left: 10px;
        margin-bottom: 2px;
      }


    }

  }


}

.myProfile__status {
  display: flex;
  align-items: center;

  img {
    margin-right: 2px;
  }

  span {
    @include fontStyle(14px, 20px, 400, 0px, $description-color);
  }
}

.chatPage {
  position: relative;
  z-index: 2000;
  top: -70px;
  background: $bg-color;

  .Chat__userInfo {
    display: flex;
    padding: 10px 20px;
    @extend %flex-element-center;
    justify-content: space-between;
    border-bottom: 1px solid #E7EBF6;
    position: relative;


    .Chat__userInfo_searchBlock {
      transform: scaleX(0);
      transform-origin: right;
      width: calc(100% - 40px);
      height: 100%;
      position: absolute;
      @extend %flex-element-center;
      background-color: #F6F7FB;
      z-index: 100;
      transition: $transition-standard;

      input {
        width: 100%;
        background-color: $input-color;
        padding: 11px;
        @include fontStyle(14px, 20px, 400, 0px, $input-text-color);
        border-radius: 5px;
        border: none;
        transition: 0.1s linear;

        &:focus {
          outline: none;
          background-color: $focus-input-color;
        }

        &:hover {
          background-color: $focus-input-color;
        }
      }
    }

    .Chat__userInfo_searchBlock_visible {
      transform: scaleX(1);

    }

    button, a {
      border: none;
      background: transparent;
      cursor: pointer;
      transition: $transition-standard;
      border-radius: 5px;
      height: 40px;
      width: 40px;
      @extend %flex-element-center;

      &:hover {
        background-color: $focus-input-color;
      }

      img {
        transform: rotate(180deg);
      }
    }

    .Chat__userInfo_searchImg {
      z-index: 200;
      width: 40px;
      height: 40px;
      @extend %flex-element-center;

      img {
        width: 25px;
        height: 25px;
        transform: none;
      }
    }

    img {
      transform: scale(1.2);
      width: 30px;
      height: 30px;
      border-radius: 50%;
    }

    &:first-of-type div {
      display: flex;
      align-items: center;

      h3 {
        @include fontStyle(16px, 23px, 500, 0px, $text-color);
        margin: 0 15px;
      }

      span {
        @include fontStyle(13px, 23px, 400, 0px, $description-color);
      }
    }
  }
}

.profile__userPhoto {
  border-radius: 50%;
  width: 86px;
  height: 86px;
  margin-bottom: 20px;
}

