        border: none;
        background: transparent;
        font-stretch: normal;
        letter-spacing: 0.25px;
      }
    }

    span {
      @include fontStyle(12px, 12px, 400, 0px, $description-color);
    }

    .privateHeading {
      position: absolute;
      right: 20px;
      top: 20px;
      width: auto;
      @include fontStyle(14px, 22px, 400, 0px, $description-color);
    }

    .myProfile__inputContainer {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .myProfile__input {
        font-stretch: normal;

        @include fontStyle(15px, 22px, 400, 0px, $text-color);
        border: none;
        width: calc(85%);
        padding: 5px 10px;
        transition: 0.1s linear;
        background: $input-color;
        border-radius: 5px;

        &:hover:not([disabled]) {
          background: $focus-input-color;
        }

        &:focus {
          outline: none;
          background: $focus-input-color;
        }
      }
    }
  }
}




